    font-family: 'MyFont';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('woff');
    font-weight: 600;
    font-style: normal;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    height: 100vh;
    width: 100vw;
    background: linear-gradient(to bottom, black, blue);
}

.title, #player-board, #computer-board, .separator {
    font-family: 'MyFont',serif;
    border-radius: 10px;
    border: 3px solid white;
    transform: translate(-50%, -50%);
    position: absolute;
    color: white;
}

.title {
    top: 15%;
    left: 50%;
    font-size: 100px;
    display: flex;
    gap: 30px;
    padding: 10px;
}

#player-board, #computer-board, .separator {
    top: 60%;
    padding: 3px;
}

#player-board, #computer-board, #init-board {
    height: calc(0.25*100vw);
    width: calc(0.25*100vw);
    display: grid;
    grid-template: repeat(10,1fr) / repeat(10,1fr);
    background-color: lightslategray;
}

#player-board {
    left: 25%;
}

#computer-board {
    left: 75%;
}

.separator {
    left: 50%;
    height: calc(0.35*100vw);
    border: none;
    background-color: burlywood;
}

.computer-item, .grid-item  {
    border: 1px solid #030303;
    background: white;
}

.ship-item {
    background-color: #1f2937;
    border: 3px solid white;
}

.water-item {
    background-color: lightblue;
}

.hit-item {
    background-color: orangered;
}

.computer-item:hover {
    background-color: lightgray;
    cursor: pointer;
}

dialog {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    border-radius: 10px;
    border: 3px solid black;
    background-color: #fde68a;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    gap: 50px;
}

dialog > button {
    border-radius: 10px;
    font-size: 20px;
    padding: 10px;
    font-family: MyFont, serif;
}

::backdrop {
    background-color: darkgray;
    opacity: 0.8;
}

.dialog-text {
    color: #1f2937;
    font-size: 40px;
    font-family: MyFont, serif;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,qBAAqB;IACrB,2DAAkD;IAClD,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,sBAAsB;IACtB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,mDAAmD;AACvD;;AAEA;IACI,2BAA2B;IAC3B,mBAAmB;IACnB,uBAAuB;IACvB,gCAAgC;IAChC,kBAAkB;IAClB,YAAY;AAChB;;AAEA;IACI,QAAQ;IACR,SAAS;IACT,gBAAgB;IAChB,aAAa;IACb,SAAS;IACT,aAAa;AACjB;;AAEA;IACI,QAAQ;IACR,YAAY;AAChB;;AAEA;IACI,wBAAwB;IACxB,uBAAuB;IACvB,aAAa;IACb,8CAA8C;IAC9C,gCAAgC;AACpC;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,SAAS;IACT,wBAAwB;IACxB,YAAY;IACZ,2BAA2B;AAC/B;;AAEA;IACI,yBAAyB;IACzB,iBAAiB;AACrB;;AAEA;IACI,yBAAyB;IACzB,uBAAuB;AAC3B;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,2BAA2B;IAC3B,eAAe;AACnB;;AAEA;IACI,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,kBAAkB;IAClB,mBAAmB;IACnB,uBAAuB;IACvB,yBAAyB;IACzB,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,aAAa;IACb,SAAS;AACb;;AAEA;IACI,mBAAmB;IACnB,eAAe;IACf,aAAa;IACb,0BAA0B;AAC9B;;AAEA;IACI,0BAA0B;IAC1B,YAAY;AAChB;;AAEA;IACI,cAAc;IACd,eAAe;IACf,0BAA0B;AAC9B","sourcesContent":["@font-face {\n    font-family: 'MyFont';\n    src: url('./resources/myFont.woff') format('woff');\n    font-weight: 600;\n    font-style: normal;\n}\n\n* {\n    box-sizing: border-box;\n    margin: 0;\n    padding: 0;\n}\n\nbody {\n    height: 100vh;\n    width: 100vw;\n    background: linear-gradient(to bottom, black, blue);\n}\n\n.title, #player-board, #computer-board, .separator {\n    font-family: 'MyFont',serif;\n    border-radius: 10px;\n    border: 3px solid white;\n    transform: translate(-50%, -50%);\n    position: absolute;\n    color: white;\n}\n\n.title {\n    top: 15%;\n    left: 50%;\n    font-size: 100px;\n    display: flex;\n    gap: 30px;\n    padding: 10px;\n}\n\n#player-board, #computer-board, .separator {\n    top: 60%;\n    padding: 3px;\n}\n\n#player-board, #computer-board, #init-board {\n    height: calc(0.25*100vw);\n    width: calc(0.25*100vw);\n    display: grid;\n    grid-template: repeat(10,